import React from 'react';
import './portfolio.scss'


function PortfolioCard(props) {
  const { data, monthData } = props;


  return (
    <div className="loans-summary">
        <div className="detail-summary">
            <p className='title'>{data[0]._id.toUpperCase()}</p>
            <p className='total'>${data[0].totalCapitalRemaining.toLocaleString(undefined, {maximumFractionDigits: 2 })}</p>
            <p className='acc-title'>AMORTIZADO</p>
            <p className='acc-total'>${data[0].totalPaid.toLocaleString(undefined, {maximumFractionDigits: 2 })}</p>
            <p className='acc-title'>TOTAL NOMINAL</p>
            <p className='acc-total'>${data[0].totalCapital.toLocaleString(undefined, {maximumFractionDigits: 2 })}</p>
        </div>
        <div className="detail-summary">
            <p className='title'>TASA REAL</p>
            <p className='total'>{((monthData.projectedInterest/monthData.projectedPrincipal) * 100).toLocaleString(undefined, {maximumFractionDigits: 2 })}%</p>
            <p className='acc-title'>TASA</p>
            <p className='acc-total'>{(data[0].averageIntRate * 100).toLocaleString(undefined, {maximumFractionDigits: 2 })}%</p>
            <p className='acc-title'>INTERESES</p>
            <p className='acc-total'>${(monthData.interestPaid).toLocaleString(undefined, {maximumFractionDigits: 2 })}</p>
            <p className='acc-title'>INTERESES PROJECTADOS</p>
            <p className='acc-total'>${(monthData.projectedInterest).toLocaleString(undefined, {maximumFractionDigits: 2 })}</p>
        </div>
        <div className="detail-summary">
            <p className='title'>CAPITAL RESTANTE MEDIO</p>
            <p className='total'>${data[0].averageLoanActiveCap.toLocaleString(undefined, {maximumFractionDigits: 2 })}</p>
            <p className='acc-title'>PRESTAMO MEDIO</p>
            <p className='acc-total'>${data[0].averageLoan.toLocaleString(undefined, {maximumFractionDigits: 2 })}</p>
            <p className='acc-title'>AMORTIZADO</p>
            <p className='acc-total'>${(monthData.principalPaid).toLocaleString(undefined, {maximumFractionDigits: 2 })}</p>
            <p className='acc-title'>AMORTIZCIN MES</p>
            <p className='acc-total'>${(monthData.projectedPrincipal).toLocaleString(undefined, {maximumFractionDigits: 2 })}</p>
        </div>
        <div className="detail-summary">
            <p className='title'>DURACION INICIAL</p>
            <p className='total'>{data[0].averageDuration.toLocaleString(undefined, {maximumFractionDigits: 2 })} meses</p>
            <p className='acc-title'># INVERSIONES</p>
            <p className='acc-total'>{data[0].totalActiveLoans}</p>
        </div>
    </div>
  );
}

export default (PortfolioCard);